import React,{useEffect} from 'react';

const SimpleGroup = ({titulo,nombre,form,setForm,autocomplete,setData,jsonFile,data}) => {



    const handleChange = (e) => {

        const temp = e.target.value;

        setForm({
            ...form,
            [e.target.name]: temp
        })

        if(autocomplete && nombre === "eps"){

            if(autocomplete.includes(temp) ){
                const temp2 = Object.keys(jsonFile[temp]);
                setData({...data,"campos": temp2 })
            }
            else if( !autocomplete.includes(temp)){
                setData({...data,"campos": [] })
            }
        }

    }

    return (
   
            
            <article className="w-3/4  p-2 mt-2" >

                <h4 className="mb-1" >{titulo}</h4>

                {!autocomplete && <input onChange={handleChange}  value={form[nombre]} autoComplete="off" className=" w-full text-black text-center " type="text"   name={nombre} />}
                
                {autocomplete &&

                    <select onChange={handleChange} value={form[nombre]} className=" w-full text-black text-center " name={nombre} >
                        <option value="" >Seleccione una opción</option>
                        {autocomplete.map((item,index) => <option key={index} value={item} >{item}</option>)}
                    </select>
                    
                }
                

            </article>


            
            
            

    );
}

export default SimpleGroup;
