import React,{useState,useEffect} from 'react'
import { PDFDocument } from 'pdf-lib';


const rename = (jsonFile,dataHook,eventName) => {
    const eps = dataHook.left.eps
    const factura = dataHook.left.factura

    let name = jsonFile[eps][eventName]
    name = name.replace("#",factura)
    name+= ".pdf"

    return name

}





export default function Rigth({data,setData,jsonFile,alerta}) {

    
    
    const [archivos,setArchivos] = useState({})

   

    useEffect(() => {

      setArchivos({})
    },[data["campos"]])
    
    const handleFile = async (e) => {

        if(e.target.files[0]  &&  data.left.factura.length > 3 ){
        const {files} = e.target

        const name  = rename(jsonFile,data,e.target.name)

        const arrFiles = []

        for (let i = 0; i < files.length; i++) {
            arrFiles.push(files[i])
        }

        
        setArchivos( {...archivos,[e.target.name]: {files:arrFiles,name} } )
        }
        else{
          
          alerta({titulo:"Campo Obligatorio",body:"diligencie el campo de factura"})
        }

        return 
    }

    

    const handleDrop = (e) => {
        e.preventDefault();
        const {files} = e.dataTransfer
        
        const name  = rename(jsonFile,data,e.target.name)
   
        


        const extension = name.split(".")[1]

        if(extension === "pdf"  ){

          if(data.left.factura.length > 3 ){

            const arrFiles = []

            for (let i = 0; i < files.length; i++) {
                arrFiles.push(files[i])
            }

        
            setArchivos( {...archivos,[e.target.name]: {files:arrFiles,name} } )

            
          }
          else{
            alerta({titulo:"Campo Obligatorio",body:"diligencie el campo de factura"})
            
          }
          
          return
        }
        alerta({ titulo: "Extension no valida",body:"Solo se aceptan archivos pdf" })
     

    }

    useEffect(() => {
        setData({
            ...data,
            right: archivos
        })
    },[archivos])
 
  return (
    <section  key={data.campos.length} className="flex w-1/2 justify-center items-center flex-col gap-5" >

    {data.campos.map((item,index) => {
      return (
        <article  className={`flex justify-center w-5/6 bg-inherit border-2 border-dotted ${ archivos[item]? "border-green-500" : "border-blue-700" }  h-16`} key={index}  >
        
          <div className="w-full h-full relative">
          <button  className={`w-full h-full absolute ${  archivos[item]? "text-green-500" : "text-blue-800"}`}>  
          
          {`${  (archivos && archivos[item]) && archivos[item].name && (archivos[item].name !=="") && archivos[item].name || item  }`}
          
          </button>
          <input value={""}  name={item} accept="application/pdf"  onChange={handleFile}  className="w-full h-16 absolute opacity-0" onDrop={handleDrop}  type="file" multiple={true} />
          
          </div>
          
        </article>
      )
    })}

    {data.campos.length === 0 && <h1 className="text-2xl font-semibold text-blue-800" >Seleccione una EPS</h1>}
    
  </section>
  )
}
