
import Left from "../components/Left";
import Rigth from "../components/Rigth";
import React,{  useState, useEffect} from 'react'
import jsonFile from "../renombre.json"

const EPS = Object.keys(jsonFile);


const alerta = (data) => {
  alert(data.titulo + " " + data.body)
}

function Index() {

 
  const [data,setData] = useState({
    left: {},
    right: {},
    campos : []
  })

  useEffect(() => {
    //pru()
  },[])

  useEffect(() => {

  },[data])

  return (
    


    <main className="container-md bg-slate-50 min-h-screen flex" >
    


    <Left data={data} setData={setData} jsonFile={jsonFile} EPSS={EPS} alerta={alerta} />

    
    <Rigth data={data} setData={setData} jsonFile={jsonFile} alerta={alerta} />




    </main>
  );
}

export default Index;
