import React,{useState} from 'react'
import axios from 'axios'

export default function Buscar() {
  const [ruta,setRuta] = useState("")
  const [factura,setFactura] = useState("")
  const handleClick = () => {

    axios.get("http://173.16.10.193:3009/api/files",{params:{factura}})
    
    .then(res => {
        
    setRuta(res.data.folder)
    })
  .catch(err => {   alert(err)    })

  }

  const Copy = () => {
    navigator.clipboard.writeText(ruta)
  }

  const handleChange = (e) => {
    setFactura(e.target.value)
  }
  return (
    <main className='container bg-slate-50 min-w-full min-h-screen'>

        <h1 className='text-center pt-10 text-2xl font-medium text-blue-800'>Busqueda Rapida de carpetas</h1>
        <section className='absolute w-full mt-24 flex flex-col items-center' >

             <article className='w-2/5 flex flex-col items-center shadow-xl border-2 rounded-sm gap-3 h-48 justify-center' >
                <div className='w-full flex  justify-center pt-4' >
                    <span className='w-12  p-1 text-center bg-blue-700 border-2
                                     rounded-tl-md rounded-bl-md border-blue-700 text-white' >FV</span>
                    <input onChange={handleChange} type="text" className='outline-none w-3/6 
                                                order-blue-700 text-center bg-inherit
                                                text-blue-800 border-blue-800 border-2  border-l-0 caret-blue-700' />
                    <button onClick={handleClick} className='ml-4 bg-blue-800 text-white rounded-sm w-1/5 border-blue-800 border-2 hover:bg-inherit hover:text-blue-800 transition-all ease-linear' >Buscar</button>
                </div>

                <div className='w-full flex  justify-center pt-2 pb-4' >
                    <span className='w-12 rounded-tl-md rounded-bl-md bg-green-500 p-1 text-right border-2 border-green-500 border-r-0 text-white' >Ruta:</span>
                    <input value={ruta} disabled type="text" className='border-l-0 outline-none w-3/6 border-2 border-green-500 bg-inherit ' />
                    <button onClick={Copy} className='ml-4 hover:bg-green-500 hover:text-white rounded-sm w-1/5 border-2 border-green-500 text-green-500  transition-all ease-linear ' >copiar</button>
                </div>
                

             </article>

            
        </section>
    </main>
  )
}
