import React,{useState,useEffect} from 'react'
import SimpleGroup from "./SimpleGroup"
import DocumentGroup from "./DocumentGroup"
import axios from "axios"



 
export default function Left({data,setData,jsonFile,EPSS,alerta}) {

  

  const [form , setForm] = useState({

    factura: "",
    nombre: "",
    eps: "",
    ruta: "\\\\173.16.10.151\\SoportesFacturacion\\FACTURAS NUEVO SOFTWARE"
  })


  useEffect(() => {
    setData({
      ...data,
      left: form
    })
  },[form])

  
  const clear = () => {

    setForm({
      factura: "",
      nombre: form.nombre,
      eps: "",
      ruta: form.ruta
    })
    setData({...data,"campos": [] })
  }

  const comunicate = (e) => {
    e.preventDefault()

    if( data.left.factura.length > 3 && data.left.nombre.length > 5 && EPSS.includes(data.left.eps) 
    
    && data.left.ruta.length > 5
    
    && data.campos.length === Object.keys(data.right).length
    
    )  
    
    {
      const formData = new FormData();
      
      const names = Object.keys(data.right).map((item) => data.right[item].name)
      
      

      formData.append("campos",data.campos)
      formData.append("names",names)
      formData.append("factura",data.left.factura)
      formData.append("eps",data.left.eps)
      formData.append("nombre",data.left.nombre.toUpperCase())

  
      
      for (const key of Object.keys(data.right)) {

        for(let i = 0; i < data.right[key].files.length; i++){
        formData.append(key,data.right[key].files[i],data.right[key].files[i].name)
        }

      }

      //console.log("formData",formData)

      const config = {     
        headers: { 'Content-Type': 'multipart/form-data' },
  
    }

      axios.post("http://173.16.10.193:3009/api/files",formData,config)
      
      .then( (res) => {  alert("Se cargo al paciente correctamente"); clear() } )
      .catch( (err) => {console.log(err)} )
      //clear()
    }

    else{
     
      alerta({titulo:"Campos Necesarios",body:"Por favor diligencie todos los campos"})
    }
    
    
  }



  return (
    <section className="bg-blue-800  min-h-screen color w-1/2 flex flex-col items-center  text-white rounded-tr-extra justify-center" >

     
    <h1 className="text-lg font-semibold mb-8" >SOFTWARE DE CREACIÓN DE CARPETAS</h1>



    <SimpleGroup titulo="Numero de la factura" nombre="factura" form={form} setForm = {setForm} />

    <SimpleGroup titulo="Nombre del facturador" nombre="nombre" form={form} setForm = {setForm}  />

    <SimpleGroup titulo="E.P.S" data={data} nombre="eps" form={form} setForm = {setForm} autocomplete={EPSS} setData={setData} jsonFile={jsonFile} />

    <article className='flex w-full justify-center gap-5'>

    <button onClick={comunicate} className="w-1/4 bg-inherit border-2 mt-9  hover:bg-white hover:text-blue-800 transition-all duration-300" >Crear</button>

    <button onClick={clear} className="w-1/4 bg-inherit border-red-600  border-2 mt-9  hover:bg-red-600 hover:text-white transition-all duration-300" >Limpiar</button>

    </article>

    <a href="/search" className='mt-12 underline underline-offset-2' target="_blank">Necesito Buscar las carpetas</a>


</section>
  )
}
